import React, {useState, useEffect} from 'react'
import { ProjectInterface } from '../global/interfaces/projectInterfaces';


import { Title, Text, Group, Button, Divider, Grid, Box, Stack } from '@mantine/core';
import { Plus } from 'tabler-icons-react';

import ProjectLink from './components/ProjectLink';
import InfoContainer from '../global/InfoContainer';
import { MainComponentOptionsInterface } from '../../containers/Main';
import NewProject from './components/NewProject';

interface ProjectsProps {
    setComponentToShow: React.Dispatch<React.SetStateAction<keyof MainComponentOptionsInterface>>
    handleProjectShow: (id: number) => void,
    route: string
    componentToShow: keyof MainComponentOptionsInterface
}



export default function Projects({
    setComponentToShow,
    handleProjectShow,
    route,
    componentToShow
}: ProjectsProps) {

    const [projects, setProjects] = useState<ProjectInterface[]>([]);

    useEffect(() => {
        fetchProjects()
    }, []);

    const fetchProjects = () => {
        fetch(route, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': "bearer " + localStorage.getItem('wilsonUserToken')
                },
            }
        )
        .then(response => response.json())
        .then(payload => setProjects(payload))
        .catch(errors => {
            console.error(errors)
        })
    }

    const renderProjectLinks = () => {
        if (projects) {
            return projects.map((project: ProjectInterface) => {
                return (
                    <ProjectLink 
                        project={{...project}}
                        handleProjectShow={handleProjectShow}
                        key={project.id}
                    />                    
                )
            })
        }
    }

    return (
        <InfoContainer 
            color='lime'
            title="Projects"
            totals={projects.length}
            contentPadding="xs"
            fullHeight
            render={
                <Stack>
                    <Group position="right">
                        <Button 
                            onClick={() => setComponentToShow("new")} 
                            leftIcon={<Plus size={14} />}
                            variant="outline"
                            color="lime"
                        >
                            New Project
                        </Button>
                    </Group>
                    <Grid 
                        grow
                    >
                        {componentToShow === "projects" && renderProjectLinks()}
                    </Grid>
                
                </Stack>
            }
        />
    )
}