import * as React from 'react';
import { Avatar, Group, UnstyledButton, Text, Menu, ThemeIcon, useMantineColorScheme} from '@mantine/core';
import { UserType } from '../../global/wilsonTypes';

import { DeviceDesktop, Logout, ChevronDown, Sun, MoonStars } from 'tabler-icons-react';
import { MainComponentOptionsInterface } from '../containers/Main';

export interface IUserDropdownProps {
    user: UserType
    setComponentToShow: React.Dispatch<React.SetStateAction<keyof MainComponentOptionsInterface>>
    logout: () => void
}

export default function UserDropdown ({
    user,
    setComponentToShow,
    logout
}: IUserDropdownProps) {

    const { colorScheme, toggleColorScheme } = useMantineColorScheme();
    const dark = colorScheme === 'dark';

    return (
        <Menu shadow="md" width={200}>
            <Menu.Target>
                <UnstyledButton

                >
                    <Group>
                        <Avatar size={40} color="blue">{user.author.initials}</Avatar>
                        <div>
                            <Text>{user.author.full_name}</Text>
                            <Text size="xs" color="dimmed">{user.email}</Text>
                        </div>
                        <ThemeIcon variant="light" color="dark">
                            <ChevronDown />
                        </ThemeIcon>
                    </Group>
                </UnstyledButton>
            </Menu.Target>

            <Menu.Dropdown>
                <Menu.Label>Application</Menu.Label>
                <Menu.Item
                    color="lime"
                    icon={<DeviceDesktop size={14} />}
                    onClick={() => setComponentToShow("projects")}
                >
                    Projects
                </Menu.Item>
                <Menu.Divider />
                <Menu.Item
                    onClick={() => toggleColorScheme()}
                    color={dark ? 'yellow' : 'blue'}
                    icon={dark ? <Sun size={14} /> : <MoonStars size={14} />}
                >
                    Toggle Light/Dark
                </Menu.Item>
                <Menu.Item 
                    color="red" 
                    icon={<Logout size={14} />}
                    onClick={logout}
                >
                    Logout
                </Menu.Item>
            </Menu.Dropdown>
        </Menu>
    );
}
