import React from 'react'
import { Box, Group, Paper, Title, Text, Divider } from '@mantine/core';
import { featuresDataType } from './interfaces/projectInterfaces';
import { BoxPadding } from 'tabler-icons-react';


interface InfoContainerProps {
    render: JSX.Element
    color?: string
    totals?: number
    title?: string
    contentPadding?: "xs" | "sm" | "md" | "lg" | "xl"
    fullHeight?: true 
}

export default function InfoContainer({
    render,
    color,
    totals,
    title,
    contentPadding,
    fullHeight
}: InfoContainerProps) {

    return (
        <Box
            sx={(theme) => ({
                borderRadius: theme.radius.sm,
                border: `solid 1px ${color}`,
                height: fullHeight ? "89.6vh" : "fit-content"
            })}
        >
            {title && 
                <>
                    <Paper
                        p={'xs'}
                    >
                        <Group
                            position="apart"
                        >
                            <Title 
                                order={4}
                                color={color}
                                className='wilson-logo-small'
                            >
                                {title}
                            </Title>
                            { totals && 
                                <Text
                                    color={color}
                                    className='wilson-logo-small'
                                >
                                    Total: {totals}
                                </Text>
                            }
                        </Group>
                    </Paper>
                    <Divider color={color} />
                </>
            }
        <Box
            sx={(theme) => ({
                // padding: padding,
                padding: contentPadding ? theme.spacing[contentPadding] : "xs",
            })}
        >

            {render}
        </Box>
        </Box>
    )
}