import React, {useState, useEffect} from 'react'
import { ProjectInterface } from '../../global/interfaces/projectInterfaces';

import { Modal } from '@mantine/core';

import ProjectForm from '../../global/containers/ProjectForm/ProjectForm';

interface EditProjectModalProps {
    project: ProjectInterface
    setProject: React.Dispatch<React.SetStateAction<ProjectInterface>>
    setModalOpen: React.Dispatch<React.SetStateAction<boolean>>
    modalOpen: true
    setFetchAgainFlag: React.Dispatch<React.SetStateAction<boolean>>
}

export default function EditProjectModal({
    project,
    setProject,
    setModalOpen,
    modalOpen,
    setFetchAgainFlag,
}: EditProjectModalProps) {

    const [editedProject, setEditedProject] = useState<ProjectInterface>({
        id: 0,
        title: "",
        description: "",
        github_url: "",
        image: "",
        author: {
            initials: "",
            full_name: "",
        }
    });

    console.log(editedProject.color)

    const handleChange = (
        e: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLTextAreaElement>
        ) => setEditedProject({...editedProject, [e.target.name]:e.target.value})

    const setColor = (
        color: string,
        name: string
    ) => {
        console.log(color)
        setEditedProject({...editedProject, "color": color, "color_name": name })
    }

    useEffect(() => {
        setEditedProject(project)
    }, [project]);

    const handleSubmit = (
        e: React.FormEvent<HTMLFormElement>
        ) => {
        e.preventDefault()
        fetch(`https://wilson-rails.herokuapp.com/api/v2/projects/${project.id}`, {
                method: 'PUT',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': "bearer " + localStorage.getItem('wilsonUserToken')
                },
                body: JSON.stringify({project: editedProject})
                })
        .then(response => response.json())
        .then(payload => {
            setProject(payload)
            setModalOpen(false)
        })
        .catch(errors => {
            console.error(errors)
        })
    }

    return (
        <Modal
            opened={modalOpen}
            onClose={() => setModalOpen(false)}
            title="Edit Project"
            size="xl"
            overflow="inside"
            transition="fade"
            transitionDuration={600}
            transitionTimingFunction="ease"
        >
            <ProjectForm 
                project={editedProject}
                setProject={setEditedProject}
                handleChange={handleChange}
                handleSubmit={handleSubmit}
                setColor={setColor}
                projectColor={editedProject.color}
            />
        </Modal>
    )
}
