import React, {useState} from 'react'
import { ProjectInterface } from '../../global/interfaces/projectInterfaces';
import { ArrowBackUp } from 'tabler-icons-react';
import { Container, Group, Title, Button, Divider, Stack } from '@mantine/core';
import ProjectForm from '../../global/containers/ProjectForm/ProjectForm';
import { MainComponentOptionsInterface } from '../../../containers/Main';
import InfoContainer from '../../global/InfoContainer';

interface NewProjectProps {
    handleProjectShow: (id: number) => void
    setComponentToShow: React.Dispatch<React.SetStateAction<keyof MainComponentOptionsInterface>>
}

export default function NewProject({
    handleProjectShow,
    setComponentToShow
}: NewProjectProps) {

    const [newProject, setNewProject] = useState<ProjectInterface>({
        title: "",
        description: "",
        github_url: "",
        image: "",
        author: {
            initials: "",
            full_name: "",
        }
    });
    
    const handleChange = (
        e: React.ChangeEvent<HTMLInputElement> | React.ChangeEvent<HTMLTextAreaElement>
        ) => setNewProject({...newProject, [e.target.name]:e.target.value})

    const setColor = (
        color: string,
        name: string
    ) => {
        setNewProject({...newProject, "color": color, "color_name": name })
    }
    
    const handleSubmit = (
        e: React.FormEvent<HTMLFormElement>
        ) => {
        e.preventDefault()
        fetch(`https://wilson-rails.herokuapp.com/api/v2/projects`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Authorization': "bearer " + localStorage.getItem('wilsonUserToken')
                },
                body: JSON.stringify({project: newProject})
                })
        .then(response => response.json())
        .then(payload => {
            setNewProject(
                {
                    title: "",
                    description: "",
                    github_url: "",
                    image: "",
                    author: {
                        initials: "",
                        full_name: "",
                    }
                }
            )
            handleProjectShow(payload.id)
        })
        .catch(errors => {
            console.error(errors)
        })
    }

    return (
        <InfoContainer
            color='lime'
            title='Create a New Project'
            contentPadding='xs'
            fullHeight
            render={
                <Stack>
                    <Group position="right">
                        <Button 
                            onClick={() => setComponentToShow("projects")} 
                            leftIcon={<ArrowBackUp size={14} />}
                            color="lime"
                        >
                            Projects
                        </Button>
                    </Group>
                    <ProjectForm
                        project={newProject}
                        handleSubmit={handleSubmit}
                        setProject={setNewProject}
                        handleChange={handleChange}
                        setColor={setColor}
                        projectColor={newProject.color}
                    />
                </Stack>
            }
        />
    )
}