import React from 'react'
import {
    Header,
    MediaQuery,
    Burger,
    useMantineTheme,
    Title
} from '@mantine/core';

import UserDropdown from '../components/UserDropdown';
import { UserType } from '../../global/wilsonTypes';
import { MainComponentOptionsInterface } from './Main';

interface TopHeaderProps {
    user: UserType
    logout: () => void
    setComponentToShow: React.Dispatch<React.SetStateAction<keyof MainComponentOptionsInterface>>
}

export default function TopHeader({
    user,
    logout,
    setComponentToShow
}: TopHeaderProps) {

    const theme = useMantineTheme();

    return (
        <Header 
            height={70} p="md"
        >
            <div 
                style={{ 
                    display: 'flex', 
                    justifyContent: 'space-between', 
                    alignItems: 'center', 
                    height: '100%',
                }}
            >
                <Title 
                    className='wilson-logo-full'
                    color={"lime"}
                >
                    Wilson
                </Title>
                { user.author && 
                    <UserDropdown
                        user={user}
                        setComponentToShow={setComponentToShow}
                        logout={logout}
                    />
                }
            </div>
        </Header>
    )
}
