import React, {useState, useEffect} from 'react'

import {
    useMantineTheme,
    AppShell,
    Navbar,
} from '@mantine/core';

import MainHeader from './containers/MainHeader';
import Main, { MainComponentOptionsInterface } from './containers/Main';
import { ROUTE } from '../../routes';
import { UserType } from '../global/wilsonTypes';

interface WilsonProps {
    logout: () => void
}

export default function Wilson({
    logout
}: WilsonProps) {

    const userRoute = `${ROUTE}/user`

    const theme = useMantineTheme();

    const [componentToShow, setComponentToShow] = useState<keyof MainComponentOptionsInterface>("projects")
    const [user, setUser] = useState<UserType>({
        first_name: "",
        last_name: "",
        email: "",
        author: {
            initials: "",
            full_name: ""
        }
    });

    console.log(user)

    useEffect(() => {
        fetchUser()
    }, []);

    const fetchUser = () => {
        fetch(userRoute, {
            method: 'GET',
            headers: {
            'Content-Type': 'application/json',
            'Authorization': "bearer " + localStorage.getItem('wilsonUserToken')
            }
        })
        .then(response => response.json())
        .then(data => {
        if (data.status === "ok") {
            setUser(data.user)
        } else {
            alert("something went wrong...")
        }
        });
    }

    return (
        <AppShell
            styles={{
                main: {
                    background: theme.colorScheme === 'dark' ? theme.colors.dark[8] : theme.colors.gray[0],
                },
            }}
            navbarOffsetBreakpoint="sm"
            asideOffsetBreakpoint="sm"
            fixed
            header={
                <MainHeader 
                    logout={logout}
                    user={user}
                    setComponentToShow={setComponentToShow} 
                />
            }
        >
            <Main 
                setComponentToShow={setComponentToShow}
                componentToShow={componentToShow}
            />
        </AppShell>
    )
}