import React, {useState} from 'react'

import Projects from '../packages/Projects/Projects';
import Project from '../packages/Project/Project'
import NewProject from '../packages/Projects/components/NewProject'
import { ROUTE } from '../../../routes';

export interface MainComponentOptionsInterface {
    projects: JSX.Element
    project: JSX.Element
    new: JSX.Element
}

interface MainProps {
    componentToShow: keyof MainComponentOptionsInterface
    setComponentToShow: React.Dispatch<React.SetStateAction<keyof MainComponentOptionsInterface>>
}

export default function Main({
    componentToShow,
    setComponentToShow,
}: MainProps) {

    const [projectShowId, setProjectShowId] = useState(0)

    const projectsRoute = `${ROUTE}/projects`
    const projectRoute = `${projectsRoute}/${projectShowId}`
    
    const renderView = (
        componentToShow: keyof MainComponentOptionsInterface,
        mainComponentOptions: MainComponentOptionsInterface
        ) => mainComponentOptions[componentToShow]

    const handleProjectShow = (
        id: number
        ) => {
        setProjectShowId(id)
        setComponentToShow("project")
    }

    const mainComponentOptions = {
        projects: <Projects 
            setComponentToShow={setComponentToShow}
            handleProjectShow={handleProjectShow} 
            route={projectsRoute}
            componentToShow={componentToShow}
        />,
        project: <Project
            id={projectShowId}
            route={projectRoute}
            setComponentToShow={setComponentToShow}
        />,
        new: <NewProject 
            handleProjectShow={handleProjectShow}
            setComponentToShow={setComponentToShow}
        />,
    }

    return (
        <div>
            {renderView(componentToShow, mainComponentOptions)}
        </div>
    )
}
